const content = document.querySelector('.content')

export function createNewPost() {
	let keys = Object.keys(localStorage)
	let titlePost = ''
	let bodyPost = ''
	for (let key of keys) {
		titlePost = key
		bodyPost = localStorage.getItem(key)
	}

	let div = document.createElement('div')
	content.append(div)
	div.className = 'post'
	div.innerHTML = `
	<h2 class="post-title">${titlePost}</h2>
	<p class="post-text hide">${bodyPost}</p>
	`
	const postTitleAll = Array.from(document.querySelectorAll('.post-title'))

	postTitleAll.forEach(elem => {
		elem.addEventListener('click', showPost)
		elem.style.backgroundColor = `rgb(${randomNum(0, 255)}, ${randomNum(0, 255)}, ${randomNum(0, 255)})`
	})

	localStorage.clear()
}

function showPost() {
	this.nextElementSibling.classList.toggle('hide')
}

function randomNum(min, max) {
	return Math.floor(Math.random() * (max - min + 1) + min)
}
